import { Component } from '@angular/core';
import { first } from 'rxjs/operators';
import { Chart } from 'chart.js';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { HttpErrorResponse } from '@angular/common/http';
import { Data } from '../Data';
import { NgxSpinnerService } from "ngx-spinner";


import { User } from '../_models';
import { UserService, AuthenticationService } from '../_services';

@Component({templateUrl: 'home.component.html'})
export class HomeComponent {
    currentUser: User;
    userFromApi: User;
    

    constructor(
        private userService: UserService,
        private authenticationService: AuthenticationService,
        private httpService: HttpClient,
        private spinner: NgxSpinnerService
    ) {
        this.currentUser = this.authenticationService.currentUserValue;
    }
 
      url = 'https://my-json-server.typicode.com/ninjaxori/database1/';
     
    //  public pieChartLebels = ['Daily task', 'Meetings', 'Trainings', 'Leaves'];
    //  public pieChartData = [120, 150,180,50];

     public pieChartType = 'pie';
    pieChartOptions = {
        responsive: true
    }
    pieChartLabels =  [ 'Daily Task', 'Training', 'Leave'];
    pieChartColor:any = [
        {
            backgroundColor: ['rgba(30, 169, 224, 0.8)',
            'rgba(255,165,0,0.9)',
            'rgba(139, 136, 136, 0.9)',
            'rgba(255, 161, 181, 0.9)',
            'rgba(255, 102, 0, 0.9)'
            ]
        }
    ]
    pieChartData:any = [
        { 
            data: []
        }
    ];
    
    
    ngOnInit() {

        this.spinner.show();
 
         setTimeout(() => {
      /** spinner ends after 5 seconds */
           this.spinner.hide();
           }, 3000);
  
        this.userService.getById(this.currentUser.id).pipe(first()).subscribe(user => { 
            this.userFromApi = user;
            
        });

        this.httpService.get(this.url + this.currentUser.firstName).subscribe(
            data => {
                this.pieChartData = data as any [];	 
            },
            (err: HttpErrorResponse) => {
                console.log (err.message);
            }
        );
        console.log (this.userFromApi.util);
}
}